import React, { useState, useEffect } from "react";
import {
  Form,
  Select,
  Button,
  Input,
  DatePicker,
  TimePicker,
} from "antd";
import dayjs from "dayjs";
import "./TodoForm.scss";

interface TodoFormProps {
  addTodo: (text: string, user: string, date: string, time: string) => void;
}

const TodoForm = ({ addTodo }: TodoFormProps) => {
  const [users, setUsers] = useState<User[]>([]);

  type User = {
    value: string;
    label: string;
  };

  useEffect(() => {
    setUsers([
      { value: "", label: "" },
      { value: "", label: "" },
      { value: "", label: "" },
      { value: "", label: "" },
    ]);
  }, []);

  const [text, setText] = useState("");
  const [user, setUser] = useState("");
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");

  // 
  const onChangeDatePicker = (date: dayjs.ConfigType, dateString: string) => {
    setDate(dateString);
  };

  // 
  const onChangeTimePicker = (time: dayjs.ConfigType, timeString: string) => {
    setTime(timeString);
  };

    // 
  const onFinishTodoCreate = () => {
    if (!text) return;
    addTodo(text, user, date, time);
    setText("");
  };

  return (
    <Form onFinish={onFinishTodoCreate} layout="inline" className="todoForm">

      <Form.Item className="formItem">
        <Select
          defaultValue=""
          onChange={(value) => setUser(value)}
          options={users}
          className="todoSelect"
        />
      </Form.Item>

      <Form.Item className="formItem">
        <Input
          type="text"
          value={text}
          onChange={(e) => setText(e.target.value)}
        />
      </Form.Item>

      <Form.Item className="formItem">
        <DatePicker onChange={onChangeDatePicker} placeholder=""/>
      </Form.Item>

      <Form.Item className="formItem">
        <TimePicker format={"HH:mm"} onChange={onChangeTimePicker} placeholder=""/>
      </Form.Item>

      <Form.Item className="formItem">
        <Button type="primary" htmlType="submit">
          
        </Button>
      </Form.Item>
    </Form>
  );
};

export default TodoForm;
